    hexToBase64(hexStr) {
      var bytes = [];
      for (var i = 0; i < hexStr.length; i += 2) {
        bytes.push(parseInt(hexStr.substr(i, 2), 16));
      }
      const buffer = new Uint8Array(bytes);
      const blob = new Blob([buffer]);
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = function (e) {
          var base64Data = e.target.result.split(",")[1];
          resolve(base64Data);
        };
        reader.onerror = function (e) {
          reject(e);
        };
        reader.readAsDataURL(blob);
      });
    },

    async testTonSdk() {
      // 1. connect
      //  create sub providers for each namespace/chain
      let nameSpacesChains = [];
      let optSpaceChains = [];

      let hasEip155 = false;

      const chains = Object.entries(this.availableChains)
        .filter((chain) => chain[1].checked)
        .map((chain) => chain[0]);
      console.log(`chains  ===> ${chains.values()}`);
      chains.forEach(function (chainId) {
        console.log(`chains.forEach chainId  ===> ${chainId}`);
        hasEip155 = true;
        nameSpacesChains.push(`eip155:${chainId}`);
      });

      optionChains.forEach(function (chainId) {
        optSpaceChains.push(`eip155:${chainId}`);
      });

      console.log(`namespaceChains  ===> ${JSON.stringify(nameSpacesChains)}`);
      var connectData = {};
      if (nameSpacesChains && nameSpacesChains.length > 0) {
        var requireData = {};
        if (hasEip155) {
          requireData.eip155 = {
            chains: nameSpacesChains.filter((item) =>
              item.startsWith("eip155")
            ),
            defaultChain: nameSpacesChains.filter((item) =>
              item.startsWith("eip155")
            )[0],
          };
        }
        console.log(`requireData  ===> ${JSON.stringify(requireData)}`);
        connectData["namespaces"] = requireData;
        console.log(JSON.stringify(connectData));
      }

      if (optSpaceChains && optSpaceChains.length > 0) {
        var optData = {
          eip155: {
            chains: optSpaceChains.filter((item) => item.startsWith("eip155")),
            defaultChain: optSpaceChains.filter((item) =>
              item.startsWith("eip155")
            )[0],
          },
        };
        connectData["optionalNamespaces"] = optData;
      }

      if (nameSpacesChains.length === 0 && optSpaceChains.length === 0) {
        //未选择链
        alert("请选择一条链");
        return;
      }
      console.log("=====>connectData ");
      console.log(connectData);

      var connectMethod;
      if (window.provider instanceof OKXUniversalConnectUI) {
        connectMethod = window.provider.openModal(connectData);
      } else {
        connectData.sessionConfig = {
          redirect: selectedReturnStrategy,
        };
        connectMethod = window.provider.connect(connectData);
      }

      await connectMethod
        .then(() => {
          // 2. set defaul chain
          this.walletAddress = "connected";
          if (this.isPC()) {
            this.showModal = false;
          }
        })
        .catch((error) => {
          console.error("发生了错误：", error);
        });
    },

    async connectTonConnect() {
      if (window.tonProvider.connected) {
        console.log("you have connected wallet");
      } else {
        console.log("APP ---connect Proof--->>>>----", this.testProofPayload);
        let connectData = {
          tonProof: this.testProofPayload,
          redirect: "tg://resolve",
          tmaReturnUrl: "tg://resolve",
        };

        if (this.isPC()) {
          if (
            this.isSafariOrChrome() &&
            !window?.TelegramWebview &&
            !window?.TelegramWebviewProxy
          ) {
            connectData.redirect = null;
          }
          console.log("Is Mobile ==>", connectData);
        } else {
          if (
            this.isSafariOrChrome() &&
            !window?.TelegramWebview &&
            !window?.TelegramWebviewProxy
          ) {
            connectData.redirect = null;
          }
          console.log("Is Mobile ==>", connectData);
        }

        if (window.tonProvider instanceof OKXTonConnectUI) {
          if (this.testProofPayload) {
            window.tonProvider.setConnectRequestParameters({
              state: "ready",
              value: { tonProof: this.testProofPayload },
            });
          }
          await window.tonProvider
            .openModal()
            .catch((error) => console.error("发生了错误：", error));
        } else if (window.tonProvider instanceof OKXTonConnect) {
          window.tonProvider.connect(connectData).then((url) => {
            console.log("Current Url ---:", url);
            if (url && this.isPC()) {
              this.tonQrValue = url;
              this.showTonModal = true;
            }
          });
        }
      }
    },

    closeModal() {
      this.showModal = false;
    },

    closeTonModal() {
      this.showTonModal = false;
    },

    isPC() {
      const userAgentInfo = navigator.userAgent;
      console.log("current device ua is ===>", userAgentInfo);

      const Agents = [
        "Android",
        "iPhone",
        "SymbianOS",
        "Windows Phone",
        "iPad",
        "iPod",
      ];
      let flag = true;
      for (let v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
          flag = false;
          break;
        }
      }
      return flag;
    },
    isSafariOrChrome() {
      const userAgentInfo = navigator.userAgent;
      const Agents = ["Safari", "Chrome"];
      let flag = false;
      for (let v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
          flag = true;
          break;
        }
      }
      return flag;
    },

    handleModalsSelect(modals) {
