    },

    addRedirectParam() {
      if (this.testTransaction) {
        const newTransaction = JSON.parse(this.testTransaction);
        newTransaction.redirect =
          this.redirect === "deeplink" ? this.redirectDeeplink : this.redirect;
        this.testTransaction = JSON.stringify(newTransaction);
      }
    },

    canChainAddRedirectParam() {
      return chainsThatCanAddRedirectParam.some((c) =>
        this.selectedChain.startsWith(c)
      );
    },
  },
  created() {
    // for (const eventName of Object.values(OKX_TON_CONNECTION_AND_TRANSACTION_EVENT)) {
    //   console.log(eventName);
    //   window.addEventListener(eventName, (event) => {
    //     console.log(eventName,event.detail)
    //     this.currentStatus = eventName
    //   });
    // }
  },

  mounted() {
    var scriptTag = document.createElement("script");
    scriptTag.src = "https://telegram.org/js/telegram-web-app.js";
    scriptTag.id = "tgsdk";
    document.getElementsByTagName("head")[0].appendChild(scriptTag);

    this.connectReady = true;
    document.title = "OKX Connect";
    var initCall;
    if (!this.withUI) {
      initCall = OKXUniversalProvider.init({
        dappMetaData: {
          name: "univer test dapp",
          icon: "https://avatars.githubusercontent.com/u/37784886",
        },
      });
    } else {
      initCall = OKXUniversalConnectUI.init({
        dappMetaData: {
          icon: "https://static.okx.com/cdn/assets/imgs/247/58E63FEA47A2B7D7.png",
          name: "OKX WalletConnect UI Demo",
        },
        actionsConfiguration: {
          returnStrategy: selectedReturnStrategy,
          modals: "all",
        },
        language: selectedLanguage,
        uiPreferences: {
          theme: selectedTheme,
        },
      });
    }

    initCall.then((provider) => {
      window.provider = provider;
      console.log("init finished:", provider);
      console.log("init finished session:", JSON.stringify(provider.session));

      if (provider.session?.namespaces) {
        const newChains = [];
        console.log("namespaces", provider.session.namespaces);
        Object.entries(provider.session.namespaces).forEach(([key, value]) => {
          if (key === "eip155") {
            value.chains.forEach((chain) => {
              newChains.push(parseInt(chain.substring(7)));
            });
          } else if (key === "aptos") {
            value.chains.forEach((chain) => {
              if (chain === "aptos:637") {
                newChains.push("aptos:mainnet");
              } else {
                newChains.push("movement:testnet");
              }
            });
          } else {
            value.chains.forEach((chain) => {
              newChains.push(chain);
            });
          }
        });

        console.log("existing chains:", newChains);
        this.initChains(newChains);
      } else {
        this.initChains([]);
      }

      provider.on("display_uri", (uri) => {
        if (this.isPC()) {
          if (uri && !this.withUI) {
            this.qrValue = uri;
            this.showModal = true;
          }
        }
        console.log("通知>>>display_uri  ==> ", uri);
      });

      provider.on("connect", (session) => {
        console.log("通知>>>get connect session", session);
      });
      provider.on("reconnect", (session) => {
        console.log("通知>>>get reconnect session", session);
        this.walletAddress = "connected";
      });
      provider.on("disconnect", ({ topic }) => {
        console.log(`通知>>>get disconnect  ${topic}`);
        this.walletAddress = "disconnecteddddd";
      });
      provider.on("session_delete", ({ topic }) => {
        console.log(`通知>>>get session_delete  ${topic}`);
      });
      provider.on("session_update", (session) => {
        console.log(`通知>>>get session_update `, JSON.stringify(session));
      });
    });

    var TON;
    if (!this.withUI) {
      console.log("---init wallet---");

      TON = new OKXTonConnect({
        metaData: {
          name: "OKX TON Connect Demo",
          icon: "https://avatars.githubusercontent.com/u/37784886",
        },
      });
    } else {
      console.log("---init wallet UI--");
      TON = new OKXTonConnectUI({
        dappMetaData: {
          name: "OKX TON Connect Demo",
          icon: "https://avatars.githubusercontent.com/u/37784886",
        },
        actionsConfiguration: {
          returnStrategy: "tg://resolve",
          tmaReturnUrl: "back",
          modals: "all",
        },
        uiPreferences: {
          theme: THEME.LIGHT,
        },
        language: "en_US",
      });
    }
    window.tonProvider = TON;

    window.tonProvider.onStatusChange(
      (walletInfo) => {
        console.log("APP ---TON.onStatusChange>>----", walletInfo);
        if (walletInfo) {
          this.tonWalletAddress = walletInfo.account.address;
          if (this.testProofPayload) {
            var checkProofData = {
              address: this.walletAddress,
              network: walletInfo.account.chain,
              public_key: walletInfo.account.publicKey,
              proof: {
                ...walletInfo.connectItems.tonProof.proof,
                state_init: walletInfo.account.walletStateInit,
              },
            };
            console.log(`checkProof => ${JSON.stringify(checkProofData)}`);
          } else {
            this.ProofPayloadVerify = ": no proof";
          }
          this.showTonModal = false;
        } else {
          this.tonWalletAddress = "disconnected";
        }
      },
      (error) => {
        this.showTonModal = false;
        if (error.code === OKX_CONNECT_ERROR_CODES.USER_REJECTS_ERROR) {
          this.tonWalletAddress = `用户拒绝链接: code(${error.code}) `;
        } else {
          this.tonWalletAddress = `链接报错: code(${error.code})  ${error.message}`;
        }
      }
    );
  },
};
</script>
